import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        appTopFooterColor: "green",
    },
    getters: {
        appPageName (state){
            var color = state.appTopFooterColor;
            var page = null;
            switch (color) {
                case 'green':
                    page = "主页"
                    break;
                case 'teal':
                    page = "音乐"
                    break;
                case 'brown':
                    page = "书"
                    break;
                case 'indigo':
                    page = "图片"
                    break;
            }
            return page;
        }
    },
    mutations: {},
    actions: {}
});
